/******************************
Developer: Jeftar Mascarenhas
Description portuguese: O código está dividido por comentários CSS que representam a página e os comentários
que estão no formato do SASS(SCSS) representão os blocos da página.
******************************/

//variables
$bg-header: #063d49;
$green: #084653;
$green-white: #136374;
$orange: #FF761A;
$white: #ffffff;
$anchor-gray: #969595;
$gray: #ccc;
$black: #000;
$danger: #D62B1E;
$success: #278E38;

//text format style
.text-uppercase {
  text-transform: uppercase;
}

.text-uppercase {
  text-transform: uppercase;
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

//basics "Insert rests"
fieldset {
  border: 0;
  outline: none;
  padding: 0;
}

//mixing
@mixin border-radius($radius) {
  -moz-border-radius: $radius;
  -webkit-border-radius: $radius;
  -ms-border-radius: $radius;
  border-radius: $radius;

}
@mixin box-shadow($shadow) {
  -moz-box-shadow: $shadow;
  -webkit-box-shadow: $shadow;
  -ms-box-shadow: $shadow;
  box-shadow: $shadow;

}
@mixin transition($property) {
  -webkit-transition: $property;
  -moz-transition: $property;
  -o-transition: $property;
  -khtml-transition: $property;
  -ms-transition: $property;
  transition: $property;
}
@mixin transform($property) {
  -webkit-transform: $property;
  -moz-transform: $property;
  -o-transform: $property;
  -khtml-transform: $property;
  -ms-transform: $property;
  transform: $property;
}

/*fonts*/
@font-face {
  //font-family: 'SegoeUI';
  //src: url('../fontes/SegoeUI.eot?#iefix') format('embedded-opentype'),  url('../fontes/SegoeUI.woff') format('woff'),
  //url('../fontes/SegoeUI.ttf')  format('truetype'),
  //url('../fontes/SegoeUI.svg#SegoeUI') format('svg');
  font-weight: normal;
  font-style: normal;
}

body {
  background-color: #f0f0f0;
  //font-family: 'SegoeUI' !important;
  font-family: 'Open Sans', sans-serif;
  /* The Browser makes its best guess with its own rendering engine */
  /* results will vary based on the browser */
  text-rendering: auto;

  /* The Browser chooses page speed over font calculations and legibility */
  text-rendering: optimizeSpeed;

  /* The Browser chooses legibility over font calculations and page speed */
  text-rendering: optimizeLegibility;

  /* The Browser chooses exact font calculations over page speed and legibility */
  text-rendering: geometricPrecision;
}
//Message error, alerts, success
.msg-warn {
  text-align: center;
  padding: 0px 0 3px 0;
  background-color: #FF761A;
  position: relative;
  z-index: 999;
  color: #fff;
  font-size: 0.8rem;
  small > i{
    color: #FFFB00;
  }

}

//buttons
button {
  cursor: pointer;
}
.btn-success{
  border-style: none !important;
  border-color: none !important;
  border: none !important;
}

/*header top*/
.header {
  width: 100%;
  background: #0A7280; //$bg-header
  position: relative;
  @include box-shadow(2px 0 2px $black);
}
.logo {
  float: left;
  width: auto;

  img{
    width: 85%;
    margin-bottom: 22px;
  }

  h1{
    margin: 0px !important;
  }
}
.box-auth {
  float: left;
  width: 100%;
  text-align: right;
  p {
    color: $white;
    font-size: 0.9rem;
  }
}
.user-name, .nav-settings{
  display:inline-block;
  padding: 0;
}
.user-name {
  font-size:0.75rem;
  color: $white;
  margin-right: 7px;
}

.nav-settings{
  background-color: rgba(0,0,0,0.1);
  border-radius: 0 0 2px 2px;
  li{
    list-style:none;
    display:inline-block;
    border-right:1px solid #175A63;
    &:last-child{
      border-right:0;
    }
  }
  a{
    display:inline-block;
    vertical-align:top;
    color:#fff;
    font-size:0.8rem;
    padding-left:10px;
    padding-right:10px;
    text-decoration:none;
    i{
      @include  transition(all 0.2s ease-in-out);
    }
    &:hover i{
      color: $orange;
    }
  }
}


/******************************
nave menu
******************************/
.menu {
  margin: 27px 0 20px 0;
  float: right;

  li {
    list-style: none;
    float: left;
    margin: 0 20px 0 0;
    padding: 0 5px 2px 5px;
    &:last-child {
      margin: 0;
    }
  }

}

.menu {
  a {
    color: $white;
    font-size: 0.9rem;
    text-transform: uppercase;
    text-decoration: none;
    text-shadow: 1px 1px 1px rgba(0,0,0,0.5);
    @include transition(all 0.2s ease-in-out);

    &:hover {
      color: $white;
      border-bottom: 1px solid $orange;
    }
  }
}

.action-login {
  float: left;
  margin: 15px 0;
  width: 100%;
}

//
/******************************
forms
******************************/
.form-group {
  margin-bottom: 15px;
}

.form-input {
  width: 100%;
  min-height: 30px;
  padding: 0px 8px;
  display: block;
  color: #666;
  font-size: 0.820em;
  background: #fff;
  border-radius: 3px;
  border: 1px solid transparent;
  &::-webkit-input-placeholder {
    color: $anchor-gray;
    font-size: 0.820em;
  }

  &:-moz-placeholder { /* Firefox 18- */
    color: $anchor-gray;
    font-size: 0.820em;
  }

  &::-moz-placeholder { /* Firefox 19+ */
    color: $anchor-gray;
    font-size: 0.820em;
  }

  &:-ms-input-placeholder {
    color: $anchor-gray;
    font-size: 0.820em;
  }
  //&:read-only {
  //  background: #ddd;
  //}

}

.form-input-default {
  border: 1px solid lighten($black, 75) ;
  color: lighten($black, 20);
}

//buttons busca aereo
.b-btn {
  display: inline-block;
  padding: 6px 18px;
  font: bold 14px 'Open Sans', sans-serif;
  white-space: nowrap;
  vertical-align: middle;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-image: none;
  border: 1px solid transparent;
  border-radius: 3px;
  text-decoration: none;
  font-weight: 600;
}

.b-btn-default {
  color: $white;
  text-transform: uppercase;
  background-color: $green;
  @include transition(all 0.2s ease-in-out);
  &:hover {
    background-color: $green-white;
    color: $orange;
  }
}

.b-btn-success {
  background-color: $success;
  color: $white;
  @include transition(all 0.2s ease-in-out);
  &:hover {
    background-color: lighten($success, 10);
    color: $white;
  }
}

.b-btn-danger {
  background-color: lighten($danger, 15);
  color: $white;
  @include transition(all 0.2s ease-in-out);
  &:hover {
    background-color: $danger;
  }
}

.b-btn-close {
  text-decoration: none;
  color: $orange;
  background-color: #084653;
}
.b-btn-lg {
  font: 400 1.1rem 'Open Sans', sans-serif !important;
}
.b-btn-sm {
  font: 400 0.78rem 'Open Sans', sans-serif !important;
}
//label custom
.label {
  font-size: 75%;
  border-radius: 3px;
  white-space: nowrap;
  color: #fff;
  vertical-align: baseline;
  padding: .25em .4em;
  text-align: left;
  line-height: 1;
  margin-bottom: 3px;
  display: inline-block;
  width: 100%;
}

.label-adults {
  background: #5CBA84;
}

.label-children {
  background: #337AB7;
}

.label-babies {
  background: #64A0D4;
}

.panel-details {
  margin: 5px 0 5px 0;
  text-align: right;

  a {
    color: $white;
    text-decoration: none;
    display: block;
    text-align: left;
    font-size: 0.85em;
    text-align: center;
    background: $orange;
    display: inline-block;
    border-radius: 2px;
    padding: 3px 8px;
  }
}

.panel-form {
  background: $white;
  padding: 10px;

  label{
    font-size:0.82rem;
  }

}

/******************************
login page
******************************/
.login {
  width: 392px;
  margin: 80px auto 0 auto;

  a {
    color: $anchor-gray;
    font-size: 0.8em;
  }
  header {
    text-align: center;
    margin-bottom: 18px;
  }

  .action-login {
    .b-btn {
      float: right;
    }
    .form-input {
      font: italic 15px 'Segoe UI';
    }
  }

}
.login-auth {
  box-shadow: 0px 1px 2px rgba(0,0,0,0.4);
  float: left;
  width: 100%;
  padding: 29px 9px 29px 8px;
  background-color: #fff;
  position: relative;
  overflow: hidden;
  border-radius: 3px 3px 0px 0px;
  header img{
    position: relative;
    animation: login-animate 1s ease-in-out alternate;
  }
}
.copyright-login{
  background-color: #084653;
  border-radius: 0px 0px 2px 2px;
  text-align: center;
  float: left;
  width: 100%;
  padding: 3px 2px;
  color: #fff;
  font-size: 0.8em;
  position: relative;
  z-index: -3;
  animation: copyright-animate 1s ease-in-out alternate;
}
@keyframes login-animate{
  from{
    opacity:0;
    transform: scale(0.9);
  }
  to{
    transform: scale(1);
  }
}@keyframes copyright-animate{
  from{
    top:-40px;

  }
  to{
    top:0px;
  }
}

/******************************
Page search ticket(busca-passagens)
******************************/
//search form section
.box-search {
  background-color: #178B9A; //$green
  padding-top: 14px;
  padding-bottom: 5px;
}

.form-search {
  fieldset {
    border: none;
    padding: 0;
    margin-top: 8px;
  }
  label.error {
    font-size: 11px;
    color: #FFFFFF;
    margin: 2px 0 0 0;
    padding: 3px;
    background: rgba(212, 50, 50, 0.75);
    border-radius: 3px;
    position: relative;
    display: block;

    &:before {
      position: absolute;
      content: '';
      top: -10px;
      left: 50%;
      margin-left: -25%;
      border-top: 5px solid transparent;
      border-left: 5px solid transparent;
      border-right: 5px solid transparent;
      border-bottom: 5px solid #A1373A;
    }
  }

  .success {
    background: rgba(57, 149, 169, .9) !important;
    &:before {
      border-bottom: 5px solid rgba(57, 149, 169, .9) !important;
    }
  }
}
.bg-azul-claro{
  background-color: #178B9A !important;
}

// airlines section
.flight-options {
  margin: 10px 0px 0px 0px;
  color: $white;
}

.type-trip {
  display: inline-block;
  margin-right: 18px;
  font-weight: 500;
  padding: 5px 10px;

  border-radius: 3px;
}
.type-trip input[type="radio"]{
  display:none;
}
.type-trip label{
  position:relative;
  padding-left: 18px;
  cursor: pointer;
  font-size: 0.9rem;

}
.type-trip label:before{
  position:absolute;
  content:'';
  width:20px;
  height:20px;
  background-color:#fff;
  left:-8px;
  border-radius:50%;
  box-shadow: 0px 0px 3px rgba(0,0,0,0.6) inset;

}

.type-trip label:after{
  position:absolute;
  content:'';
  width:10px;
  height:10px;
  background-color:#CACACA;
  left:-3px;
  top:3px;
  border-radius:50%;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.4) inset;
}

.type-trip input[type="radio"]:checked + label:after{

  /* Background gradient */
  background: #ffa84c; /* Old browsers */
  background: -moz-radial-gradient(center, ellipse cover,  #ffa84c 0%, #ff7b0d 100%); /* FF3.6-15 */
  background: -webkit-radial-gradient(center, ellipse cover,  #ffa84c 0%,#ff7b0d 100%); /* Chrome10-25,Safari5.1-6 */
  background: radial-gradient(ellipse at center,  #ffa84c 0%,#ff7b0d 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffa84c', endColorstr='#ff7b0d',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */

}
.type-trip input[type="radio"]:checked + label:before{
  box-shadow: 0px 0px 5px rgba(0,0,0,0.6);
  animation:radio-animate 0.5s ease-in-out alternate;
}

@keyframes radio-animate{
  from{
    box-shadow: 0px 0px 2px 6px rgba(0,0,0,0.3);
  }
  to{
    box-shadow: 0px 0px 3px rgba(0,0,0,0.6);
  }
}

.airline {
  height: 100%;
  //background: rgba(0, 0, 0, .2);
  min-height: 253px;
  padding: 0px 5px;
  border-radius: 3px;
  h1 {
    padding-top: 10px;
    font-size: 0.9rem;
    font-weight: normal;
    text-transform: none;
    color: $white;

    i {
      color: $orange;
    }
  }
}
.companies {
  margin-top: 34px;
}
.company {
  width: 100px;
  display: inline-block;
  border-radius: 4px;
  margin: 0px 11px 18px 0px;
  padding-bottom: 4px;
  vertical-align: top;
  position: relative;
  background-color: rgba(199,199,199,0.8);
  label{
    cursor: pointer;
    &:before{
      content: 'Você desmarcou';
      position: absolute;
      font-size: 0.610rem;
      bottom: -14px;
      left: 3px;
      background-color: rgba(0,0,0,0.3);
      color: #fff;
      padding: 0px 5px;
      border-radius: 4px;
      transition: all .2s ease-in-out;
      z-index:3;
    }
  }
  img {
    max-width: 60px;
    position: relative;
    top: 4px;
    left: 6px;
    -webkit-filter:invert(0.4);
    @include transition(all .3s ease-in-out);
  }

  input[type='checkbox'] {
    position: absolute;
    top: 4px;
    right: -25px;
    display: none;
 }
}

.company label span{
  position: absolute;
  padding: 13px;
  top: 0;
  right: 0;
  border-radius: 0 4px 4px 0px;
  background: #FFF;
  box-shadow: inset -1px 0px 1px rgba(0,0,0,0.5);
  &:before {
    content: '';
    position: absolute;
    top: 10px;
    left: 5px;
    opacity: 0;
    height: 0px;
    transform: rotate(-50deg);
    transition: all 0.5s ease-in-out;
  }
  &:after {
    content: '';
    position: absolute;
    opacity: 0;
    top: 3px;
    left: 13px;
    width: 2px;
    height: 0px;
    transform: rotateZ(50deg);
    transition: all 1.5s ease-in-out;
  }
}

.company input[type='checkbox']{
  &:checked + label span:before {
    transition: all 0.5s ease-in-out;
    width: 2px;
    opacity: 1;
    height: 8px;
    z-index: 3;
    background: #ffa84c;
    background: -moz-radial-gradient(center, ellipse cover, #ffa84c 0%, #ff7b0d 100%);
    background: -webkit-radial-gradient(center, ellipse cover, #ffa84c 0%,#ff7b0d 100%);
    background: radial-gradient(ellipse at center, #ffa84c 0%,#ff7b0d 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffa84c', endColorstr='#ff7b0d',GradientType=1 );
  }
  &:checked + label span:after {
    transition: all 0.5s ease-in-out;
    opacity: 1;
    height: 15px;
    z-index: 3;
    background: #ffa84c;
    background: -moz-radial-gradient(center, ellipse cover, #ffa84c 0%, #ff7b0d 100%);
    background: -webkit-radial-gradient(center, ellipse cover, #ffa84c 0%,#ff7b0d 100%);
    background: radial-gradient(ellipse at center, #ffa84c 0%,#ff7b0d 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffa84c', endColorstr='#ff7b0d',GradientType=1 );
  }
  &:checked + label img{
    -webkit-filter:invert(0.0);
  }
  &:checked + label:before {
    bottom: -8px;
    opacity:0;
  }
}

.box-flight {

  fieldset {
