  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#0a7280+0,0a7280+50,0a7280+100 */
  background: #0a7280; /* Old browsers */
  background: -moz-radial-gradient(center, ellipse cover,  #0a7280 0%, #0a7280 50%, #0a7280 100%); /* FF3.6-15 */
  background: -webkit-radial-gradient(center, ellipse cover,  #0a7280 0%,#0a7280 50%,#0a7280 100%); /* Chrome10-25,Safari5.1-6 */
  background: radial-gradient(ellipse at center,  #0a7280 0%,#0a7280 50%,#0a7280 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0a7280', endColorstr='#0a7280',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */

}

